.marker {
  transform: translate(-50%, -100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}

.marker > label {
  max-width: 200px;
  text-align: center;
}

.marker > .title {
  color: #ffb23f;
  background-color: white;
  border: solid #ffb23f 1px;
  border-radius: 8px;
  padding: 4px;
  width: max-content;
  max-width: 80px;

  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

  margin-bottom: 0px;
}

.marker > .address {
  color: #5f5f5f;
  background-color: white;
  border: solid #4f36ff 1px;
  border-radius: 8px;
  padding: 4px;
  width: max-content;

  margin-bottom: 0px;
}

.marker > .icon {
  padding: 0px;
  margin: 0px;
  width: 30px;
  height: 30px;
}
